<template>
  <div class="tourist-attraction">
    更新时间：2020.09.27
   <div class="province" :key="index" v-for="(province, index) in touristAttraction">
     <div class="flex-box flex-wrap province-item">
      <div class="province-title">{{ province.label }}</div>
      <div class="flex-1 flex-box flex-wrap">
        <div class="city-title" :class="{
          'city-title-cur': cur.label === city.label && cur.index === index
        }" :key="index2" v-for="(city, index2) in province.list" @click="handle(city, index)">{{ city.label }}</div>
      </div>
    </div>
    <div v-if="cur.index === index" class="flex-box flex-wrap list">
      <p :key="i" v-for="(text, i) in cur.list">{{text}}</p>
    </div>
   </div>
  </div>
</template>

<script>
import TouristAttraction from '@/utils/tourist-attraction.json'
export default {
  name: 'TouristAttraction',
  data () {
    return {
      touristAttraction: TouristAttraction,
      cur: {
        index: '',
        label: '',
        list: []
      }
    }
  },
  methods: {
    handle (data, index) {
      this.cur = {
        index: index,
        label: data.label,
        list: data.list
      }
    }
  }
}
</script>

<style scoped lang="less">

.tourist-attraction {
  --width: 70px;

  padding: 0 5px 5px;
  .province {
    margin-top: 5px;
  }
  .province-item {
    &:hover {
      background: #eee;
    }
  }
  .province-title {
    width: var(--width);
    text-align: center;
    font-weight: bold;
  }
  .city-title {
    padding: 0 6px;
    cursor: pointer;
    &:hover {
      background: #42b983;
      color: white;
    }
  }
  .city-title-cur {
    background: #42b983;
    color: white;
  }
  .list {
    margin-left: var(--width);
    background: #42b983;
    color: white;
    padding: 7px 14px;
    p {
      margin-right: 1em;
    }
  }
}
</style>
